.square{
    display: flex;
    align-items: center;
    width: 100%;
}

.square-area{
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 4px
}


.square-area input{
    display: flex;
    align-items: center;
    width: 80%;
    height: 1.8rem;
    padding: 5px 12px 5px 12px;
    border-radius: 0.2rem;
    border: 1px solid rgba(122, 122, 122, 1);
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25) inset;

}   

.square-area select{
    display: flex;
    align-items: center;
    width: 80%;
    height: 2.5rem;
    padding: 5px 12px 5px 12px;
    border-radius: 0.2rem;
    border: 1px solid rgba(122, 122, 122, 1);
    box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25) inset;
}

.leave-margin{
    margin-top: 3rem;
}

.error-exta{
    color: red;
    margin-top: 3rem;
    margin-left: 4rem;
}

